import { object, string } from 'yup';

export const schemaWithDescription = object({
  imageTitle: string()
    .min(2, '    2- !')
    .max(50, '    50- !')
    .required('    !'),
  imageURL: string()
    .min(5, '    5- !')
    .required('    !'),
  description: string()
    .min(5, '    5- !')
    .max(80, '    80- !')
    .required('    !'),
}).required();

export const schemaWithoutDescription = object({
  imageTitle: string()
    .min(2, '    2- !')
    .max(50, '    50- !')
    .required('    !'),
  imageURL: string()
    .min(5, '    5- !')
    .required('    !'),
}).required();
